import { createApp } from 'vue'
import App from './App.vue'
import store from './store'
import State from './store/State.js'

class Osm_app
{
	app: any;

	/**
	 * init
	*/
	init(config: Object)
	{
		this.app = createApp(App).use(store);
		this.app.provide('config', config).mount('#osm_app');
	}

	/**
	 * destroy
	*/
	destroy()
	{
		this.app.unmount();
		this.app = '';
	}

	/**
	 * dump
	*/
	dump()
	{
		console.dir( JSON.parse( JSON.stringify(store.state) ) );
	}

	/**
	 * insert_new_bearer
	*/
	insert_access_token(string: string)
	{
		State.save_bearer(string);
	}
}

(<any>window).osm_app = new Osm_app;
