<template>
	<div class="form-check">
		<input class="form-check-input" type="checkbox" :id="field_id" :name="field_id" v-model="value" true-value="1" false-value="0" @change="changed()" :disabled="disabled">
		<label class="form-check-label" :for="field_id">
			{{ checkbox_label }}
		</label>
	</div>
</template>

<script>
	import State from '@/store/State.js';
	import Functions from '@/mixin/tools/Functions.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		data() {
			return {
				value: '0',
			}
		},
		methods: {
			changed() {
				State.save_user_input(this.field_id, this.value);
			},
		},
		computed: {
			disabled() {
				if (this.field_data.disabled === 'true') {
					return true;
				}
				return false;
			},
			checkbox_label() {
				return this.field_data.label;
			},
		},
		mounted() {
			this.value = Functions.value_from_storage_or_session(this.field_id, this.field_data, 0);
		},
	}
</script>
