<template>
	<Preloader v-if="preloader_state" id="preloader" />
	<Loading v-show="show_loading" />
</template>

<script>
	import State from '@/store/State.js';
	import Preloader from '@/components/modals/Preloader.vue';
	import Loading from '@/components/modals/Loading.vue';

	export default {
		props: {
		},
		components: {
			Preloader,
			Loading,
		},
		data() {
			return {
			}
		},
		methods: {
		},
		computed: {
			show_loading() {
				return State.is_loading();
			},
			// move to State when more modals are added
			preloader_state() {
				return State.get('modals').preloader;
			},
		},
	}
</script>
