<template>
	<div v-for="(item, order) in this.field_data.enum" :class="['form-check', radio_layout]">
		<input class="form-check-input" type="radio" :name="field_id" :id="radio_id(order)" :value="order" :disabled="disabled" v-model="value" @change="changed()">
		<label class="form-check-label" :for="radio_id(order)">
			{{ label(item) }}
		</label>
	</div>
</template>

<script>
	import State from '@/store/State.js';
	import Functions from '@/mixin/tools/Functions.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		data() {
			return {
				value: 0,
			}
		},
		methods: {
			radio_id(order) {
				return this.field_id + '-' + order;
			},
			label(item) {
				for (var key in item) {
					if (! item.hasOwnProperty(key)) { continue; }
					return item[key];
				}
			},
			changed() {
				State.save_user_input(this.field_id, this.value);
			},
		},
		computed: {
			disabled() {
				if (this.field_data.disabled === 'true') {
					return true;
				}
				return false;
			},
			radio_layout() {
				if (this.field_data.type_settings.radio_layout === 'horizontal') {
					return 'form-check-inline';
				} else if (this.field_data.type_settings.radio_layout === 'icons') {
					return 'form-check-inline input-button-icon';
				} else if (this.field_data.type_settings.radio_layout === 'vertical') {
					return '';
				} else if (this.field_data.type_settings.radio_layout === 'blank') {
					return 'form-check-inline input-button-blank';
				} else {
					console.log('undefined radio_layout: ' + this.field_id);
					return '';
				}
			},
		},
		mounted() {
			this.value = Functions.value_from_storage_or_session(this.field_id, this.field_data, 0);
		},
	}
</script>
