<template>
	<section class="formulare">
		<div class="row">
			<div class="col-md-12">

				<Tabs :group_form="group_form" :tab_current="tab_current" :errors="errors" />

				<Prev_next_buttons @prev="prev()" @next="next()" :show_back="show_back" />

			</div>
		</div>
	</section>
</template>

<script>
	import Prev_next_buttons from '@/components/nav/Prev_next_buttons.vue';
	import Group_form from '@/mixin/requests/Group_form.js';
	import Tabs from '@/components/forms/Tabs.vue';
	import State from '@/store/State.js';

	export default {
		props: {
		},
		components: {
			Prev_next_buttons,
			Tabs,
		},
		data() {
			return {
				group_form: {},
				errors: {},
				tab_current: 1,
				tab_last: 0,
			}
		},
		methods: {
			prev() {
				if (this.tab_current > 1) {
					this.tab_current--;
				}
			},
			next() {
				if (this.tab_current < this.tab_last) {
					Group_form.validategroupform();
					this.tab_current++;
					return;
				}
				State.loading('ON');
				Group_form.validategroupform().then(
					(response) => {
						if (response.result !== undefined && response.result.validform) {
							State.change_step(2);
						} else {
							var validation = response.result.form_validation_error_elements;
							if (validation !== undefined && typeof validation === 'object') {
								this.errors = validation;
								this.show_first_tab_with_error();
							}
						}
						State.loading('OFF');
					}
				).catch(
					(error) => { throw error; }
				);
			},
			show_first_tab_with_error() {
				for (var tab_number in this.group_form) {
					if ( ! this.group_form.hasOwnProperty( tab_number ) ) { continue; }
					for (var input_order in this.group_form[tab_number]) {
						if ( ! this.group_form[tab_number].hasOwnProperty( input_order ) ) { continue; }
						for (var input_id in this.group_form[tab_number][input_order]) {
							if ( ! this.group_form[tab_number][input_order].hasOwnProperty( input_id ) ) { continue; }
							if (this.errors.hasOwnProperty(input_id)) {
								this.tab_current = parseInt(tab_number, 10);
								return;
							}
						}
					}
				}
			},
		},
		computed: {
			show_back() {
				return (this.tab_current > 1) ? true : false;
			},
		},
		created() {
			State.loading('ON');
			Group_form.getgroupform().then(
				(response) => {
					if (response.status !== 200) {
						return;
					}
					State.store_session_params_do_not_overwrite(response.result);
					this.group_form = response.result.form;
					this.tab_last = Object.keys(this.group_form).length;
					State.loading('OFF');
				}
			).catch(
				(error) => { throw error; }
			);
		},
	}
</script>
