<template>
	<form id="formular">
		<div id="myTabContent" class="tab-content">

			<div class="no-print tab-pane row active">
				<div class="row">

					<h4>Tab {{ tab_current }}</h4>

					<Wrap v-for="(field, order) in active_tab" :key="order" :field_data="field_data(field)" :field_id="field_id(field)" :errors="errors" />

				</div>
			</div>

		</div>
	</form>
</template>

<script>
	import Wrap from '@/components/forms/Wrap.vue';
	import Show_hide from '@/mixin/tools/Show_hide.js';

	export default {
		props: {
			group_form: Object,
			tab_current: Number,
			errors: Object,
		},
		components: {
			Wrap,
		},
		data() {
			return {
			}
		},
		methods: {
			field_id(field) {
				for ( var key in field ) {
					if ( ! field.hasOwnProperty( key ) ) { continue; }
					return key;
				}
			},
			field_data(field) {
				for ( var key in field ) {
					if ( ! field.hasOwnProperty( key ) ) { continue; }
					return field[key];
				}
			},
			filter_show_hide(inputs) {
				var clear = {};
				for ( var order in inputs ) {
					if ( ! inputs.hasOwnProperty( order ) ) { continue; }
					// Show_hide for input with its wrap
					var show_hide_instance = new Show_hide(inputs[order]);
					if (show_hide_instance.visibility_boolean(inputs[order])) {
						clear[order] = inputs[order];
					}
				}
				return clear;
			},
		},
		computed: {
			active_tab() {
				var tab = this.group_form[this.tab_current];
				tab = this.filter_show_hide(tab);
				return tab;
			},
		},
	}
</script>
