<template>
	<div :class="columns" :data-show="show" :data-hide="hide">
		<div class="validation-placeholder validation-message">
			<span v-for="(value, key) in has_error">
				<span class="mark-circle mark-orange">!</span>
				{{ value }}
			</span>
		</div>
		<div :class="['input-block', required, blank, 'd-flex align-items-start align-items-sm-center flex-column', label_size, error_class]">
			<div v-if="label" class="input-label">
				{{ label }}
			</div>
			<div :class="['input-area', input_block_class]">
				<Input_switcher v-for="(value, key) in inputs" :key="key" :field_data="value" :field_id="key" />
			</div>
			<div v-if="tooltip" class="ttip ttip-left">
				?
				<span class="ttip_text">{{ tooltip }}</span>
			</div>
		</div>
	</div>
</template>

<script>
	import Input_switcher from '@/components/forms/Input_switcher.vue';
	import Show_hide from '@/mixin/tools/Show_hide.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
			errors: Object,
		},
		components: {
			Input_switcher,
		},
		data() {
			return {
			}
		},
		methods: {
		},
		computed: {
			inputs() {
				var inputs = {};
				// separate single inputs from block inputs
				if (this.field_data.elements === undefined) {
					// normal way where every field contain single input
					inputs[this.field_id] = this.field_data;
				} else {
					// some fields are blocks of multiple inputs
					for ( var key in this.field_data.elements ) {
						if ( ! this.field_data.elements.hasOwnProperty( key ) ) { continue; }
						// Show_hide inside block
						var input = { [key]: this.field_data.elements[key] };
						var show_hide_instance = new Show_hide(input);
						if (show_hide_instance.visibility_boolean()) {
							inputs[key] = this.field_data.elements[key];
						}
					}
				}
				return inputs;
			},
			has_error() {
				for ( var id in this.inputs ) {
					if ( ! this.inputs.hasOwnProperty( id ) ) { continue; }
					if ( this.errors.hasOwnProperty( id ) ) {
						return this.errors[id];
					} else {
						return [];
					}
				}
			},
			error_class() {
				if (this.has_error.length === 0) {
					return '';
				} else {
					return 'validation-error';
				}
			},
			columns() {
				if ( this.field_data.block_settings !== undefined && this.field_data.block_settings !== null ) {
					var size = this.field_data.block_settings.size;
				} else if ( this.field_data.type_settings !== undefined && this.field_data.type_settings !== null ) {
					var size = this.field_data.type_settings.size;
				} else {
					return '';
				}
				if ( size === 'full' ) {
					return 'col-12';
				} else if ( size === 'half' ) {
					return 'col-12 col-lg-6';
				} else if ( size === 'third' ) {
					return 'col-12 col-lg-4';
				} else {
					return 'col-12';
				}
			},
			required() {
				return '';
				// required is temporarily turned off on client's request
				// return 'input-required';
			},
			blank() {
				if ( this.field_data.type_settings !== undefined && this.field_data.type_settings.design === 'blank' ) {
					return 'input-block-blank';
				} else {
					return '';
				}
			},
			show() {
				if ( this.field_data.elements === undefined ) {
					return this.field_data['data-show-condition'];
				} else {
					return this.field_data['block_show_condition'];
				}
			},
			hide() {
				if ( this.field_data.elements === undefined ) {
					return this.field_data['data-hide-condition'];
				} else {
					return this.field_data['block_hide_condition'];
				}
			},
			label_size() {
				if ( this.field_data.type_settings !== undefined && this.field_data.type_settings.label_size === 'fullwidth' ) {
					return 'input-label-full';
				} else {
					return 'flex-sm-row';
				}
			},
			label() {
				if ( this.field_data.type === 'checkbox' ) {
					// simple checkboxes doesnt have label
					return false;
				} else if (
					this.field_data.block_settings !== undefined
					&&
					this.field_data.block_settings.block_layout === 'checkbox_collapse'
				) {
					// block checkbox_collapse doesnt have label
					return false;
				} else {
					// every other input has label
					if ( this.field_data.elements === undefined ) {
						// simple fields uses own label
						return this.field_data.label;
					} else {
						// blocks uses block label
						return this.field_data.block_label;
					}
				}
			},
			input_block_class() {
				if ( this.field_data.elements === undefined ) {
					// normal way where every field contain single input
					return '';
				} else {
					// some fields are blocks of multiple inputs
					// .input-mixed determine the input is block type, few block layouts adds additional class
					if ( this.field_data.block_settings.block_layout === 'select_text' ) {
						return 'input-mixed input-select-text';
					} else if ( this.field_data.block_settings.block_layout === 'checkbox_collapse' ) {
						return 'input-mixed input-onoff';
					} else {
						return 'input-mixed';
					}
				}
			},
			tooltip() {
				if ( this.field_data.description !== undefined && this.field_data.description !== '' ) {
					return this.field_data.description;
				} else if ( this.field_data.block_input_hint !== undefined && this.field_data.block_input_hint !== '' ) {
					return this.field_data.block_input_hint;
				} else {
					return false;
				}
			},
		},
	}
</script>
