<template>
	<div class="flatpickr_scope input-datepicker">
		<input ref="input" type="text" :id="field_id" :name="field_id" v-model="value" @change="changed()">
	</div>
</template>

<script>
	import State from '@/store/State.js';
	import Functions from '@/mixin/tools/Functions.js';
	import flatpickr from 'flatpickr';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		data() {
			return {
				value: '',
			}
		},
		methods: {
			changed() {
				State.save_user_input(this.field_id, this.value);
			},
		},
		computed: {
		},
		mounted() {
			this.value = Functions.value_from_storage_or_session(this.field_id, this.field_data, '');

			// this.field_data.type_settings.calendar_type === 'standard' | 'years_first' - not needed anymore

			flatpickr(
				this.$refs.input,
				{
					static: true,
					dateFormat: 'd.m.Y',
					defaultDate: this.value,
					allowInput: true,
				}
			);
		},
	}
</script>

<style scoped lang="scss">
	.flatpickr_scope:deep() {
		@import '@/assets/libraries/flatpickr_4.6.9/flatpickr.scss';
	}
</style>
