<template>
	<div :class="['osm_app_scope', skin]">
		<div :class="['container', loading_class]">
			<div class="page-wrap">
				<View_switcher />
			</div>
			<Modal_switcher />
		</div>
	</div>
</template>

<script>
	import State from '@/store/State.js';
	import View_switcher from '@/components/View_switcher.vue';
	import Modal_switcher from '@/components/Modal_switcher.vue';
	import { version } from '@/../package.json';

	export default {
		components: {
			View_switcher,
			Modal_switcher,
		},
		inject: ['config'],
		data() {
		},
		methods: {
		},
		created() {
			State.save_the_config(this.config);
			console.log('osm_app:', version);
		},
		computed: {
			skin() {
				return State.skin_class();
			},
			loading_class() {
				if (State.is_loading()) {
					return 'loading';
				} else {
					return '';
				}
			},
		},
	}
</script>

<style scoped lang="scss">
	.osm_app_scope:deep() {
		@import '@/style_imports.scss';
	}
	.loading > .page-wrap {
		opacity: 0.6;
		pointer-events: none;
	}
</style>
