<template>
	<select v-model="value" class="form-control" :id="field_id" :name="field_id" :disabled="disabled" @change="changed()">
		<option value="" disabled="disabled">--- vyber ---</option>
		<option v-for="(item, key) in field_data.enum" :value="option_value(item)">
			{{ option_label(item) }}
		</option>
	</select>
</template>

<script>
	import State from '@/store/State.js';
	import Functions from '@/mixin/tools/Functions.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		data() {
			return {
				value: '',
			}
		},
		methods: {
			changed() {
				State.save_user_input(this.field_id, this.value);
			},
			option_value(item) {
				for ( var key in item ) {
					if ( ! item.hasOwnProperty( key ) ) { continue; }
					// we have two types of enum options, one straight with key=>value, other also with useless category data key=>object
					if (typeof item[key] === 'string') {
						return key;
					} else if (typeof item[key] === 'object') {
						return item[key].value;
					}
				}
			},
			option_label(item) {
				for ( var key in item ) {
					if ( ! item.hasOwnProperty( key ) ) { continue; }
					// we have two types of enum options, one straight with key=>value, other also with useless category data key=>object
					if (typeof item[key] === 'string') {
						return item[key];
					} else if (typeof item[key] === 'object') {
						return item[key].label;
					}
				}
			},
		},
		computed: {
			select_2_class_style() {
				// does nothing, for now, not implemented yet
				/*if ( field_data.type_settings.select_type === 'simple' ) {
					content = content.replace( '{{select_2_class}}', '' );
					content = content.replace( '{{select_2_style}}', '' );
				} else if ( field_data.type_settings.select_type === 'searchable' ) {
					content = content.replace( '{{select_2_class}}', 'select2_initialize select2' );
					content = content.replace( '{{select_2_style}}', 'style="width:100%"' );
				} else if ( field_data.type_settings.select_type === 'large_ochorenie' ) {
					content = content.replace( '{{select_2_class}}', 'large_select' );
					content = content.replace( '{{select_2_style}}', '' );
				} else {
					// console.log( 'undefined select_type for: ' + field_id );
					content = content.replace( '{{select_2_class}}', '' );
					content = content.replace( '{{select_2_style}}', '' );
				}*/
			},
			disabled() {
				if (this.field_data.disabled === 'true') {
					return true;
				}
				return false;
			},
		},
		mounted() {
			this.value = Functions.value_from_storage_or_session(this.field_id, this.field_data, '');
		},
	}
</script>
