import State from '@/store/State.js';
import Functions from '@/mixin/tools/Functions.js';
import Request_service from '@/mixin/main_flow/Request_service.js';

/**
 * Group_form
*/
class Group_form
{
	/**
	 * getgroupform
	*/
	getgroupform()
	{
		let requestParams = {};
		requestParams.action_name = 'getgroupform';
		requestParams.method = 'GET';
		requestParams.url = State.endpoint_url() + 'online-sale-mw/online/getgroupform/' + State.product_group_id();
		requestParams.url += '?';
		requestParams.url += Functions.field_format_parameter();
		requestParams.url += Functions.add_token_as_parameter();
		requestParams.url += Functions.global_parameter();

		let req = new Request_service;
		return req.prepare_params( requestParams ).handle_response();
	}

	/**
	 * validategroupform
	*/
	validategroupform()
	{
		let requestParams = {};
		requestParams.action_name = 'validategroupform';
		requestParams.body = Functions.serialize_user_input();
		requestParams.body += State.add_agent_code();
		requestParams.method = 'POST';
		requestParams.url = State.endpoint_url() + 'online-sale-mw/online/validategroupform/' + State.product_group_id();
		requestParams.url += '?';
		requestParams.url += Functions.add_token_as_parameter();
		requestParams.url += Functions.global_parameter();

		let req = new Request_service;
		return req.prepare_params( requestParams ).handle_response();
	}
}

export default new Group_form;
